//测试地址
const webURL = "http://192.168.0.183:8080"
//正式地址
// const host = "http://uat.jrerdangjia.com";

export default class Http {
    static async post(api, paramObj) {
        let user_id = localStorage.getItem("user_id")
        let token = localStorage.getItem("token");
        paramObj = { ...paramObj, user_id }
        let url = webURL + api,
            body = getBodyString(paramObj);
        let params = {
            method: 'POST',
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded',
                Authorization: token,
            },
            body: body,
        }
        return fetch(url, params)
            .then(res => res.json())
            .catch(err => console.log(err))

    }

    static async get(api, query) {
        query = query || {}
        let user_id = localStorage.getItem("user_id")
        let token = localStorage.getItem("token");
        var myHeaders = new Headers();
            myHeaders.append("authorization",token);
        let params = {
            method: 'GET',
            headers: myHeaders
        }
        let queryString = getQueryString({
            ...query,
            user_id
        })
        let url = webURL + api + queryString;
        return fetch(url, params)
            .then((res) => res.json())
            .catch(err => console.log(err))
    }
}

function getBodyString(obj) {
    let keyArray = Object.keys(obj),
        str = keyArray.map((res, index) => {
            if (index !== keyArray.length - 1)
                return res + '=' + obj[res] + '&';
            else
                return res + '=' + obj[res];
        }).join('');
    return str;
}

function getQueryString(queryObj){
    const keyArray = Object.keys(queryObj);
    let query = ''
    keyArray.forEach((res, index) => {
        const condi = index === 0 ? '?' : '&';
        query = query + condi + res + '=' + queryObj[res];
    });
    return query
}


