import React, { Component } from 'react'
import Http from '../../services/http'

const getOpenId = (code) => {
    const url = "wechat/server/openid"
    const params = {
        code
    }
    return Http.get(url, params)
}

export default class Launch extends Component {
    constructor(props) {
        super(props)
        this.state = {}
    }
    api = {
        checkLogin: "/api/checkAdminToken"
    }
    
    async componentDidMount() {
        const { history } = this.props;
        let { checkLogin } = this.api

        let loginInfo = await Http.get(checkLogin, {})
        let isLogin = loginInfo.type

        let place = isLogin ? 'wrongbanklist' : 'login'
        history.replace(place)
    }

    render() {
        return (<div></div>)
    }
}


