const gd = localStorage.getItem('globalData');
const globalData = gd ? JSON.parse(gd) : {};
const defaultState = {
    errorQuestionList: [],
    currentQuestion: {}
};
const reducer = (state = defaultState, action) => {
    // console.log(action)
    const { type, payload } = action;
    switch (type) {
        case "setErrList":
            state = {
                ...state,
                errorQuestionList: payload
            }
            return state
        case "setCurrentQuestion":
            state = {
                ...state,
                currentQuestion: payload
            }
            return state
        default:
            return state
    }
}
export default reducer


